import { type Readable, derived, get, writable } from "svelte/store"
import type { Schema } from "@api"
import { language } from "@pages/language"
import type { Store as AppliedProductsStore } from "./applied-products"
import type { Store as SkinCareStore } from "./skin-care/skin-care"
import type { Product } from "./types"

type Category = {
  type: string
  name: string
  categoryType: string
  active?: boolean
}

type ProductsListProduct = Product & { active: boolean }

type ProductType = {
  type: string
  categoryType: string
}

const types = writable<ProductType[]>([])
const items = writable<Schema.Product[]>([])

const ProductNameByProductType = derived(language, ($language) => ({
  EYES: "Eyes",
  EYELINER: $language.eyeliner,
  EYESHADOW: $language.eyeshadow,
  EYELASHES: $language.mascara, // TODO: remove the temporary fallback when back-end fixed the type to MASCARA
  EYEBROWS: "Eyebrow pencil",
  MASCARA: $language.mascara,
  BLUSH: $language.blush,
  FOUNDATION: $language.foundation,
  LIPSTICK: $language.lipstick,
  HIGHLIGHTER: $language.highlighter,
  CONTOUR: $language.contour,
  CONCEALER: $language.concealer,
  LIPLINER: $language.lipliner,
  GLOSS: $language.lipGloss,
  BB_CC_CREAM: $language.bbAndCCcream,
  EYES_AND_BROWS: $language.eyesAndBrows,
  CHEEKS: $language.cheeks,
  FACIAL_MASK: "Facial Mask",
  SERUM: "Serum",
  EXFOLITATORS: "Exfolitators",
  HYDRATING_CREAM: "Hydrating Cream",
  ANTI_AGEING_CREAM: "Anti-ageing Cream",
  TONER: "Toner",
  EYES_CREAM: "Eyes cream",
  ANTI_ACNE: "Anti-acne",
  ANTI_WRINKLE: "Anti-wrinkle",
}))

export const data = { types, items }

type SeasonalColorAnalysisStore = Readable<{
  season: "WINTER" | "AUTUMN" | "SUMMER" | "SPRING"
} | null>

export const create = (
  appliedProducts: AppliedProductsStore,
  sca: SeasonalColorAnalysisStore,
  skinCare: SkinCareStore
) => {
  const activeCategory = writable<Category["type"]>()
  const categories = derived(
    [types, activeCategory, ProductNameByProductType],
    ([$types, $active, $ProductNameByProductType]) => {
      return $types.map(({ type, categoryType }) => ({
        type,
        categoryType,
        name: $ProductNameByProductType[type as keyof typeof $ProductNameByProductType],
        active: type === $active,
      }))
    }
  )

  const products: Readable<ProductsListProduct[]> = derived(
    [items, appliedProducts, sca, skinCare.enabled, skinCare.recommendationList],
    ([$items, $appliedProducts, $sca, $skinCareEnabled, $skinCareRecommendationList]) => {
      const activeIds = new Set($appliedProducts.map((p) => p.id))

      return $items.map((item) => {
        const product: ProductsListProduct = {
          ...item,
          active: activeIds.has(item.id),
        }

        if ($skinCareEnabled) {
          if ($skinCareRecommendationList.has(product.type)) {
            product.recommendation = "recommended"
          }
        } else if ($sca) {
          if (product.isRecommended) product.recommendation = "recommended"
        }

        return product
      })
    }
  )

  const productsByCategory = derived([products, categories], ([$products, $categories]) => {
    const active = $categories.find((c) => c.active)!
    return $products.filter((p) => p.type === active?.type)
  })

  const changeCategory = ({ type }: Category) => activeCategory.set(type)

  const setDefaultCategory = (categoryType: Schema.CategoryType) => {
    const defaultCategory = get(categories).find((item) => item.categoryType === categoryType)

    if (defaultCategory) {
      changeCategory(defaultCategory)
    }
  }

  const unsubscribe = types.subscribe(($types) => {
    if (!$types.length) return

    const active = get(activeCategory)
    if (!active) activeCategory.set($types[0].type)

    setTimeout(unsubscribe)
  })

  return {
    categories,
    items: productsByCategory,

    setDefaultCategory,
    changeCategory,

    select: appliedProducts.add,
    clear: appliedProducts.remove,
  }
}
