export type Dictionary = typeof english | typeof portuguese
export type Language = "english" | "portuguese"

const languages: Record<Language, Dictionary> = {
  english: english,
  portuguese: portuguese,
}

export const language = writable<Dictionary>(english)
export const setActiveLanguage = (_language: Language) => language.set(languages[_language])
